.areaToDoList {
  display: flex;
  height: 100vh;
  justify-content: center;
  align-items: center;
  background: linear-gradient(90deg, #69b7eb, #8fc0b6, #7ad9c2);
}

.container {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-flow: column;
  width: 35vw;
  height: 80vh;
  background-color: #cc74a4;
  box-shadow: -20px -20px 0px 0px rgb(100 100 100 / 10%);
}

.headerTitle {
  width: 90%;
}

.pinIcon {
  width: 40px;
  height: 40px;
}

.subTitle {
  display: flex;
  font-family: sans-serif;
  font-size: 1.2em;
  color: #FFFF;
  margin-top: 15px;
}

.titleToDoList {
  display: flex;
  font-family: sans-serif;
  font-size: 3em;
  color: #FFFF;
  margin: 0px;
  justify-content: space-between;
}

.containerItems {
  height: 65%;
  width: 100%;
  overflow-y: auto;
  margin-bottom: 5px;
}

.containerItems::-webkit-scrollbar {
  width: 8px;
}

.containerItems::-webkit-scrollbar-track {
  background: rgb(243, 237, 226);
}

.containerItems::-webkit-scrollbar-thumb {
  background-color: rgb(127, 127, 128);
  border-radius: 40px;
}

.messageArea {
  display: flex;
  width: 100%;
  height: 80%;
  justify-content: center;
  align-items: center;
}

.message {
  display: flex;
  flex-flow: column;
  font-family: sans-serif;
  font-size: 3em;
  color: #FFFF;
}

.downIcon {
  display: flex;
  margin-left: 50%;
  width: 30px;
  height: 30px;
}


@media (max-width: 850px) {
  .container {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-flow: column;
    width: 85vw;
    height: 80vh;
    background-color: #cc74a4;
    box-shadow: -20px -20px 0px 0px rgb(100 100 100 / 10%);
  }
}